.gg-chevron-right {
	top: -20px;
	box-sizing: border-box;
	position: relative;
	display: block;
	-webkit-transform: scale(1);
	        transform: scale(1);
	-webkit-transform: scale(var(--ggs, 1));
	        transform: scale(var(--ggs, 1));
	width: 22px;
	height: 22px;
	border: 2px solid transparent;
	border-radius: 100px;
	cursor: pointer;
	outline: none;
}

.gg-chevron-right::after {
	content: "";
	display: block;
	box-sizing: border-box;
	position: absolute;
	width: 10px;
	height: 10px;
	border-bottom: 2px solid;
	border-right: 2px solid;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
	right: 6px;
	top: 4px
}

.gg-chevron-left {
	top: -20px;
	box-sizing: border-box;
	position: relative;
	display: block;
	-webkit-transform: scale(1);
	        transform: scale(1);
	-webkit-transform: scale(var(--ggs, 1));
	        transform: scale(var(--ggs, 1));
	width: 22px;
	height: 22px;
	border: 2px solid transparent;
	border-radius: 100px;
	cursor: pointer;
	outline: none;
}

.gg-chevron-left::after {
	content: "";
	display: block;
	box-sizing: border-box;
	position: absolute;
	width: 10px;
	height: 10px;
	border-bottom: 2px solid;
	border-left: 2px solid;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	left: 6px;
	top: 4px
}

.gg-chevron-right.invalid, .gg-chevron-left.invalid {
	display: none;
}

