header {
	text-align: center;
	position: fixed;
	z-index: 999;
	width: 100%;
	background-color: #fff;
	top: 0;
	font-weight: 300;
	height: 64px;
}

.logo {
	text-decoration: none;
	width: 100%;
	height: 64px;
	margin: 0;
	padding: 0;
	position: relative;
	left: 0;
	top: 0;
	color: black;
}

.logo p {
	position: relative;
	margin: 24px;
	font-size: 1.2rem;
}

.nav-toggle-label {
	position: absolute;
	top: 0;
	left: 0;
	margin-left: 1em;
	height: 100%;
	display: flex;
	align-items: center;
}

.nav-toggle-label span, .nav-toggle-label span::before, .nav-toggle-label span::after {
	display: block;
	background: var(--dark-bg-color);
	height: 2px;
	width: 2em;
	border-radius: 2px;
	position: relative;
	cursor: pointer;
}

.nav-toggle-label span::before, .nav-toggle-label span::after {
	content: '';
	position: absolute;
}

.nav-toggle-label span::before {
	bottom: 7px;
}

.nav-toggle-label span::after {
	top: 7px;
}

nav {
	position: absolute;
	text-align: left;
	top: 100%;
	left: 0;
	width: 100%;
	-webkit-transform: scale(1, 0);
	        transform: scale(1, 0);
	-webkit-transform-origin: top;
	        transform-origin: top;
	-webkit-transition: -webkit-transform 400ms ease-in-out;
	transition: -webkit-transform 400ms ease-in-out;
	transition: transform 400ms ease-in-out;
	transition: transform 400ms ease-in-out, -webkit-transform 400ms ease-in-out;
	background-color: #fff;
}

nav ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

nav li {
	margin-bottom: 1em;
	margin-left: 1em;
}

nav a {
	color: black;
	text-decoration: none;
	font-size: 1.2rem;
	text-align: center;
	padding: 16px 14px;
	border-radius: 4px;
}

nav.mobile-nav {
	-webkit-transform: scale(1, 1);
	        transform: scale(1, 1);
}

.nav-toggle-label:focus {
	display: none;
}

.current-page {
	background-color: #21409a;
	color: #fff;
}

@media (min-width: 800px) {
	.nav-toggle-label {
		display: none;
	}
	header {
		display: grid;
		height: 64px;
	}
	nav {
		position: relative;
		text-align: left;
		-webkit-transition: none;
		transition: none;
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
		background: none;
		top: auto;
		top: initial;
		left: auto;
		left: initial;
		grid-column: 3 / 4;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}
	nav ul {
		position: relative;
		display: flex;
		background-color: #fff;
		right: 25%;
	}
	.logo p {
		width: 100%;
		margin: 0;
		position: relative;
		top: 50%;
		-webkit-transform: translate(0, -50%);
		        transform: translate(0, -50%);
	}
	nav li {
		margin-left: 3em;
		margin-bottom: 0;
	}
	nav a {
		opacity: 1;
		position: relative;
	}
	nav a::before {
		content: '';
		display: block;
		height: 5px;
		position: absolute;
		top: -0.75em;
		left: 0;
		right: 0;
	}
	nav a:hover:not(.current-page):not(.logo) {
		background-color: #21409a;
		color: white;
	}
	.navbar-container.dark .current-page {
		background: var(--dark-secondary);
	}
	.navbar-container.dark a:hover:not(.current-page):not(.logo) {
		background-color: var(--dark-secondary);
		color: white;
	}
}

