		align-self: center;
		justify-self: center;
	}
	.landing-content p {
		text-align: center;
	}
	.text-content {
		width: 100%;
		display: flex;
		align-content: center;
		justify-content: center;
	}
	.contact-content {
		display: flex;
		width: 80%;
		text-align: center;
		flex-direction: column;
		align-content: center;
		justify-content: center;
	}
	.contact-content p {
		width: 100%;
		text-align: center;
	}
	.image {
		display: none;
	}
	.contact-page{
		display: flex;
		align-items: center;
		justify-items: center;
		justify-content: center;
	}

	.contact-content{
		width:100%;
	}
}


.dark.contact-page {
	background-color: var(--dark-bg-color);
	color: white;
}

.dark.contact-page .office {
	color: var(--dark-secondary)
}
